/*

  Popover

  Usage: Typically triggered by information icons 
    and error messages attached to fields

*/
.popover {
  display: inline-block;
  position: absolute;
  width: 180px;
  background: #eeeeee; }
  @media print, screen and (min-width: 40em) {
    .popover {
      width: 290px; } }
  .popover:before {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    border: 10px solid;
    -webkit-transform: rotate(360deg); }
  .popover.top {
    left: -8px;
    bottom: 50px; }
    .popover.top:before {
      border-color: #eeeeee transparent transparent transparent;
      left: 10px;
      bottom: -20px; }
  .popover.bottom {
    left: 0px;
    top: 0px; }
    .popover.bottom:before {
      border-color: transparent transparent #eeeeee;
      left: 30px;
      top: -20px; }
  .popover.withIcon {
    padding-left: 80px; }
  .popover__body {
    display: inline-block;
    padding: 10px 40px 10px 10px; }
  .popover__icon {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 30%;
    left: 20px; }
  .popover__title {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 0px; }
  .popover__text {
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 0px; }
  .popover p:last-of-type {
    margin-bottom: 0px; }

/* 
loop through colours again and change before background
*/
/* USE CASE: ERROR */
.error {
  color: #fefefe; }
  .error .popover {
    position: relative;
    top: -10px;
    background: #E60014; }
    .error .popover:before {
      border-color: transparent transparent #E60014; }



